import CommonHeader from '@/components/layouts/CommonHeader.vue';
import confirmLogin from '@/components/confirmLogin.vue';
export default {
	components: {
		CommonHeader,
		confirmLogin
	},
	data() {
		return {
			showconfirmLogin: true,
			currentSwiper: 0,
			banners: [],
			category: [],
			subCategory: [],
			sigleBanner: {},
			winHeight: "", //窗口高度
			currentTab: 0, //预设当前项的值
			scrollLeft: 0, //tab标题的滚动条位置
			productList: [],
			hasMore: 1,
			title: '',
			currentOrder: 0,
			orderType: 0,
			sub_category_id: 0
		}
	},
	onLoad(e) {
		this.title = e.title
		this.sub_category_id = e.category
		this.getList()
	},
	methods: {
		changeOrder(current){
			let type = this.orderType
			if(current == this.currentOrder){
				if(type == 0){
					this.currentOrder = current
					this.orderType = 2
				}else if(type == 2){
					this.currentOrder = current
					this.orderType = 1
				}else if(type == 1){
					this.currentOrder = 0
					this.orderType = 0
				}
			}else{
				this.currentOrder = current
				this.orderType = 2
			}
			
			this.getList()
		},
		// 商品列表
		getList() {
			uni.request({
				url: getApp().globalData.api + 'product/index',
				method: 'GET',
				data: {
					open_id: uni.getStorageSync('open_id'),
					sub_category_id: this.sub_category_id,
					order: this.currentOrder,
					order_type: this.orderType
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					this.productList = res.data.data.list
					this.hasMore = res.data.data.has_more
				}
			});
		},
		toProduct(id) {
			uni.navigateTo({
				url: "/pages/product/product_info?id=" + id
			})
		}
	}
}
